@-webkit-keyframes contact_style_spinner__38tX4 {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    90% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(365deg);transform: translate3d(-50%, -50%, 0) rotate(365deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);transform: translate3d(-50%, -50%, 0) rotate(360deg);}
}
@keyframes contact_style_spinner__38tX4 {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
    90% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(365deg);transform: translate3d(-50%, -50%, 0) rotate(365deg);}
    100% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(360deg);transform: translate3d(-50%, -50%, 0) rotate(360deg);}
}

/* loading spinner - inside */
@-webkit-keyframes contact_style_reverse-spinner__20w_y {
    0% {-webkit-transform: translate3d(-50%, -50%, 0) rotate(0deg);transform: translate3d(-50%, -50%, 0) rotate(0deg);}
